import i18next from 'i18next';
import React, { useState } from 'react'
import { useTranslation } from 'react-i18next';
import { Link, useNavigate } from 'react-router-dom';

const toggleIt =() =>{
    let main=document.querySelector('.main');
    let aside= document.querySelector('.aside');
    if (main){
        if(!main.classList.contains('hide'))
            main.classList.add('hide');
    }
    if(aside){
        if(aside.classList.contains('hide'))
            aside.classList.remove('hide')
    }
}

const HideIt =() =>{
    let main=document.querySelector('.main');
    let aside= document.querySelector('.aside');
    if (main){
        if(main.classList.contains('hide'))
            main.classList.remove('hide');
    }
    if(aside){
        if(!aside.classList.contains('hide'))
            aside.classList.add('hide')
    }
}
const goAbout=(name:string,gohom:any)=>{
    let slideH=0, expH=0, skillH=0, careerH;
    const slider=document.querySelector('.slider');
    const exp= document.querySelector('.experiences');
    const skill= document.querySelector('.skills')
    const career= document.querySelector('.careers')

    HideIt();
    if(slider && exp && skill && career){
        slideH=slider.getBoundingClientRect().height;
        expH=exp.getBoundingClientRect().height;
        skillH=skill.getBoundingClientRect().height;
        careerH=career.getBoundingClientRect().height;

        if(name==="A Propos" || name==="About"){
            gohom()
            window.scrollTo({
                top:slideH-20,
                left:0,
                behavior:'auto'
            });

        }
        
        else if(name=== "Experiences")
            window.scrollTo({
                top:(slideH*2)-50,
                left:0,
                behavior:'auto'
            });

        else if(name=== "Compétences" || name=== "Skills")
            window.scrollTo({
                top:slideH*2 +expH +60,
                left:0,
                behavior:'auto'
            });

        else if(name==="Parcours" || name==="Trainings")
            window.scrollTo({
                top:(slideH+50) + expH*2 +(skillH*2),
                left:0,
                behavior:'auto'
            });
    }
}

const Aside = (gohom:any) => {
    
    const {t} = useTranslation()
    const nav =t('navigation').split('/');
    return(
        <div className='column space-between  align-center aside-content'>
            <div className="quit row j-center align-center pointer" onClick={()=>goAbout("",gohom)}>
                X
            </div>
            {
                nav.map(v=>{
                    return(
                        <div key={v} className="link pointer" onClick={()=>goAbout(v,gohom)}>{v}</div>
                        )
                    })
                    
                }
        </div>
    )
}

const Header=() =>{
    const [activelang, setActivelang] = useState('fr');

    const [isSunny, setIsSunny] = useState(true)

    const navigate =useNavigate()
    const gohom = (home="")=>{
        HideIt();
        navigate('/portfolio')
    }

    const changeLang=(lang:string)=>{
        setActivelang(lang);
        i18next.changeLanguage(lang)
    }
    const changeMode = () =>{
        const switcher=document.querySelector('.switch');
        if(switcher){
            Array.from(switcher.children).forEach(child=>{
                if(child.classList.contains('hide'))
                child.classList.remove('hide')
                else
                child.classList.add('hide');
            })
        }
        const about= document.querySelector('.about');
        const main= document.querySelector('.main');
        const dText= document.querySelectorAll('.default-txt');

        if(isSunny){
            localStorage.setItem("mode",'dark')
            if(main){
                if(!main.classList.contains('dark'))
                    main.classList.add('dark');
            }
            if(about){
                if(!about.classList.contains('dark')){
                    about.classList.add('dark');
                }
            }
            Array.from(dText).forEach(dt=>{
                if(!dt.classList.contains('text-light'))
                    (dt as HTMLElement).style.color="#fff"
            })
        }else{
            localStorage.setItem("mode",'sun')
            if(main){
                if(main.classList.contains('dark'))
                    main.classList.remove('dark');
            }
            if(about){
                if(about.classList.contains('dark')){
                    about.classList.remove('dark');
                }
            }
            Array.from(dText).forEach(dt=>{
                if(!dt.classList.contains('text-light'))
                    (dt as HTMLElement).style.color="#000"
            })
        }
        
        
        setIsSunny(isSunny=>!isSunny);
    }

    const {t} = useTranslation()
    const nav =t('navigation').split('/');
return (
    <div className="row space-between text-light">
        <div className="col-12 aside hide">
            <Aside gohom={gohom}/>
        </div>
        <div className="logo col-2 row align-center">
            <div className="row align-center">
                <div className="toggle" onClick={() => toggleIt()}>
                    <i className="fa-solid fa-bars pointer" />
                </div>
                <a href="https://bit.ly/3irX8L4" target="_blank">
                    <img className='small-profile' src="/portfolio/Images/logoyoutube.png" alt="" />
                </a>
                <div className='switch relative' onClick={()=>changeMode()}>
                    <i className="sun c-cv pointer">☀</i>
                    <i className="fa-solid fa-moon c-blue pointer hide float-r" />
                </div>
            </div>
        </div>

        <ul className='row ul-navs space-between col-6 align-center'>
        <Link to="/portfolio" className="link"><i className="fa-solid fa-house" /></Link>
            {
                nav.map(v=>{
                    return(
                        <li key={v} className="link" onClick={()=>goAbout(v,gohom)}>{v}</li>
                    )
                })
            }
        </ul>
        <ul className="row space-between align-center col-3 f-end profile">
            <Link to='/portfolio/project'className="link">{t('pj')}</Link>
            <a href="https://bit.ly/3irX8L4" target="_blank" className="link">
                <i className="fa-brands fa-youtube c-red" />
            </a>
            <a href="https://bit.ly/3JBK69I" target="_blank" className="link">
                <i className="fa-brands fa-linkedin c-blue" />
            </a>
            <a href="https://bit.ly/3IBTpVP" target="_blank" className="link text-center">
                <i className="fa-brands fa-github" />
            </a>
            <li className="link">
                {/* <i className="fa-solid fa-language c-blue" /> */}
                <div className="dropdown">
                    <i className="fa-solid fa-language c-blue" />
                    <i className="fas fa-caret-down"></i> {' '}
                    <ul className="dropdown-content card">
                        <li className={'row j-center align-center ' /*+(activelang==='fr' ?"lang-active":'')*/} onClick={()=>changeLang('fr')}>
                            <img className='icon' src="/portfolio/Images/Flag_of_France.svg" alt=""/>
                            <p>Français</p>
                        </li>

                        <li className={'row j-center align-center '/*+(activelang==='en' ?"lang-active":'')*/} onClick={()=>changeLang('en')}>
                            <img className='icon' src="/portfolio/Images/Flag_of_the_United_Kingdom.png" alt=""/>
                            <p>English</p>
                        </li>
                    </ul>
                </div>
            </li>
            <li className="row align-center link" onClick={()=>goAbout("about",gohom)}>
                <img className='small-profile' src="/portfolio/Images/profile.png" alt="" />
            </li>
        </ul>
    </div>
)
}

export default Header