import React, { FC, useState } from "react";
import { useTranslation } from "react-i18next";
import InView from "react-intersection-observer";
import { HandleIntersect } from "../Components/About";
import LoadingPie from "../Components/LoadingPie";

const data=["#e7d807","#2e1222","#723d00","#242206","#2c085c","#9b7200","#007213","#003b72","#700101","#001696","#111116","#960358"]
interface Props{
    title: string;
    MySkills: any
}
const CompetencesScreen: FC<Props>=(props:Props) => {
    const MySkills=props.MySkills;
    const keys=Object.keys(MySkills);

    const [indexkey, setindexKey] = useState(0);

    const nextSkill=()=>{
        if(indexkey===keys.length-1)
            setindexKey(0)
        else(setindexKey(indexkey=>indexkey+1));
    }

    const backSkill=()=>{
        if(indexkey==0)
            setindexKey(keys.length-1)
        else(setindexKey(indexkey=>indexkey-1));
    }
    const {t} = useTranslation()
    return( 
    <div className="skills column">
        <div className="row space-between align-center">
        <div className='col-1 row j-center fs-2 pointer' onClick={()=>backSkill()}>
            <i className="chevron fa-solid fa-chevron-left"></i>
        </div>
        <div className="competences col-10 relative">
            <InView as="div" onChange={(inView: boolean, entry: IntersectionObserverEntry) => HandleIntersect(inView,entry)} className="cover-elt col-12">
            <div className="skill-type row j-center align-center border-radius">
                {t(MySkills[keys[indexkey]].title)}
            </div>

            <div className="skills-content absolute row col-12 space-around">
                {
                    MySkills[keys[indexkey]].skills.map((skill:any)=>{
                        return(
                            <div className="skill-card column align-center j-center" key={skill.name} style={{color:skill.color}}>
                                <LoadingPie value={90} color={"cyan"}/>
                                <p className="text-center"><i className={skill.icon}/> {skill.name}</p>
                            </div>
                        )
                        }
                    )
                }
            </div>
            
        </InView>
        </div>
        <div className='col-1 row j-center fs-2 pointer' onClick={()=>nextSkill()}>
            <i className="chevron fa-solid fa-chevron-right"></i>
        </div>

        </div>
    </div>
    );
}

export default CompetencesScreen;