import React, { FC } from "react";
import InView from "react-intersection-observer";
import { HandleIntersect } from "./About";
import { useTranslation } from 'react-i18next';

interface Props{
    experience: {
        _id: number;
        img: string;
        title: string;
        missions: string[];
        date: string;
        bg:string,
        color:string
    }
}
const Experiences:FC<Props>=(props:Props)=>{
    const experience=props.experience;
    const {t} = useTranslation();
    const about=document.querySelector('about');
    if(about){
        if(!localStorage.issun){
            if(!about.classList.contains('dark'))
            about.classList.add('dark');
        }
    }
    return (
        <InView as="div" onChange={(inView: boolean, entry: IntersectionObserverEntry) => HandleIntersect(inView,entry)} className="cover-elt col-12 default-txt">
        <div className="experience border border-radius"style={{background:experience.bg}}>
            <div className="row align-center relative">
                <div className="xper col-2 row space-between" style={{backgroundImage:"url(/portfolio/Images/"+experience.img}}>
                </div>
                <div className="col-10">
                    <h3 style={{color:experience.color}} className="text-center">{t(experience.title)}</h3>
                    
                    {
                        experience.missions.map((miss,i)=>{
                            return(
                                <p key={i+1} className="padding0-1">
                                    ✓ {t(miss)}
                                    <br />
                                    <br />
                                </p>
                            )
                        })
                    }
                </div>
            </div>
                
                <div className="col-4 date">
                    <h3>{t(experience.date)}</h3>
                </div>
            </div>
                </InView>    
    )
}

export default Experiences