import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next';
import Typewriter from 'typewriter-effect';

import '../css/slider.css'
const Slider=() =>{
    const [cpt, setCpt] = useState(0);
    const [contents, setContents] = useState(document.querySelector('.contents'))
    const [slider, setSlider] = useState(document.querySelector('.slider'))
    const [screenWidth, setScreenWidth] = useState(0)
    const [decal, setDecal] = useState(0)
    let x:Element[]=[];
    const [sliders, setSliders] = useState(x)

    const next = () => {
        setCpt(cpt=>cpt+1)
        let contents = document.querySelector('.contents')
        if(contents){
            setContents(contents)
            setSliders(Array.from(contents.children))
            if (cpt === sliders.length - 1) {
                setCpt(0)
            }
            setSlider(document.querySelector('.slider'))
            if(slider){
                setScreenWidth(slider.getBoundingClientRect().width)
                setDecal(-screenWidth * cpt);
                (contents as HTMLElement).style.transform = `translateX(${decal}px)`
            }
        }
    }

    const prev = () => {
        setCpt(cpt=>cpt-1)
        let contents = document.querySelector('.contents')
        if(contents){
            setContents(contents)
            setSliders(Array.from(contents.children))
            if (cpt <=0) {
                setCpt(sliders.length - 1)
            }
            setSlider(document.querySelector('.slider'))
            if(slider){
                setScreenWidth(slider.getBoundingClientRect().width)
                setDecal(-screenWidth * cpt);
                (contents as HTMLElement).style.transform = `translateX(${decal}px)`
            }
        }
    }

    useEffect(()=>{
        const timer=window.setInterval(next,4000);
        return ()=>{
            clearInterval(timer);
        }
    },)

    const {t} = useTranslation();

    return(
        <div className="slider col-12">
            <div className='col-12 contents flex'>

                {/* <div className="filter col-12"></div> */}
                <div className="s-content">
                    <div className="col-12 row j-center">
                        <div className="col-12 row text-center">
                            <div className='s-des col-6'>
                                <h3 className=''>{t('coucou')}, {t('im')} </h3>
                                <h1 className='my-name'>Bah Thierno</h1>
                            </div>

                            <div className='slide-l text-light'>
                                <h1>
                                    <Typewriter
                                        options={
                                            {
                                                loop:true
                                            }
                                        }
                                        onInit={(typewriter) => {
                                        typewriter.typeString(`<strong>Dev, </strong>`)
                                            .pauseFor(500)
                                            .typeString('<strong style="color:cyan";>C/C++!</strong>')
                                            .pauseFor(500)
                                            .deleteChars(7)
                                            .pauseFor(500)
                                            .typeString('<strong style="color:yellow;"> Java!</strong>')
                                            .pauseFor(500)
                                            .deleteChars(5)
                                            .pauseFor(500)
                                            .typeString('<strong style="color:yellow;"> Full-Stack!</strong>')
                                            .start();
                                        }}
                                        
                                    />
                                </h1>
                                <div className="col-12 bg-head">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="s-content">
                    <a href='https://bit.ly/3irX8L4'  target="_blank" className=" col-12 row j-center">
                        <div className="col-12 row align-center text-center">
                            <div className='s-des col-6'>
                                <h1 className='default-txt'>Phoenix Coder</h1>
                                <i className="my-name fa-brands fa-youtube c-red" />
                            </div>
                            
                            <div className='col-6 phoenix-coder relative slide-l'>
                                <img className='col-4 border-radius' src="/portfolio/Images/logoyoutube.png" alt="" />
                            </div>
                        </div>
                        <div className='default-txt'>
                            <h1>
                                <Typewriter
                                    options={
                                        {
                                            loop:true,
                                            
                                        }
                                    }
                                    onInit={(typewriter) => {
                                    typewriter.typeString(`<strong>P H O E N I X -  C O D E R</strong>`)
                                        .pauseFor(500)
                                        .deleteAll()
                                        .start();
                                    }}
                                    
                                />
                            </h1> 
                            <div className="bg-head"></div>
                        </div>
                        
                    </a>

                </div>

            </div>
            <div className="s-directions">
                <div className="s-left"  onClick={()=>prev()}>
                    <i className="fa-solid fa-chevron-left"></i>
                </div>
                <div className="s-right" onClick={()=>next()}>
                    <i className="fa-solid fa-chevron-right"></i>
                </div>
            </div>

            {/* <div className="col-12 line bottom"></div> */}

        </div>
    )
}

export default Slider;