import React from 'react'

import Typewriter from 'typewriter-effect';

import Slider from './../Components/Slider';
import About from './../Components/About';
import CompetencesScreen from './CompetencesScreen';
import Experiences from '../Components/Experiences';
import {careers, experiences, MySkills } from '../data.js';
import { useTranslation } from 'react-i18next';
import Formations from '../Components/Formations';

const HomeScreen = () => {
    const {t} = useTranslation()
return (
    <div>
        <Slider/>
        <div className="container">
        <About/>

        <h1 className=" row j-center align-center text-center c-head h1-title default-txt">
            <i className="fa-solid fa-grip-lines" /> 
            <Typewriter
                        options={
                            {
                                loop:true
                            }
                        }
                        onInit={(typewriter) => {
                        typewriter.typeString('Experiences')
                            .pauseFor(500)
                            .deleteChars(7)
                            .start();
                        }}
                        
                    />
            <i className="fa-solid fa-grip-lines" />
        </h1>

        <div className="experiences">
            {
                experiences.map(experience=>{
                    return(
                        <div className="container" key={experience._id}>
                    <Experiences experience={experience}/>
                </div>
                )
            })
        }

            
        </div>

        <h1 className="row j-center align-center text-center c-head h1-title default-txt"> 
            <i className="fa-solid fa-grip-lines" /> 
            {t('skills')}
            <i className="fa-solid fa-grip-lines" />
        </h1>
        <CompetencesScreen title={''} MySkills={MySkills}/>

        <h1 className="row j-center align-center text-center c-head h1-title default-txt"> 
            <i className="fa-solid fa-grip-lines" /> 
            <Typewriter
                options={
                    {
                        loop:true
                    }
                }
                onInit={(typewriter) => {
                typewriter.typeString('Formations')
                    .pauseFor(500)
                    .deleteChars(7)
                    .start();
                }}
                
            />
            <i className="fa-solid fa-grip-lines" />
        </h1>
        <div className="careers">
            <Formations/>
        </div>

    </div>

    </div>
)
}

export default HomeScreen