import React, { FC } from 'react'
import "react-circular-progressbar/dist/styles.css";
import { buildStyles, CircularProgressbar } from 'react-circular-progressbar';

interface Props{
    value: number;
    color: string
}

const  LoadingPie:FC<Props>=(props:Props) => {
    const value=props.value
    const color=props.color
    return (
        <div className='col-5 box box-padding'>
            <CircularProgressbar
                value={value}
                text={`${value}%`}
                styles={buildStyles({
                        textColor: "#FF9408",
                        pathColor: color,
                        trailColor: "#eec"
                    })
                }
            />
        </div>
    )
}

export default LoadingPie;