import React from 'react';
import InView from 'react-intersection-observer';
import { HandleIntersect } from './About';
import { useTranslation } from 'react-i18next';

const Contact = () => {
    const {t} = useTranslation()
    return <div className='contact-me container'>
        <InView as="div" onChange={(inView: boolean, entry: IntersectionObserverEntry) => HandleIntersect(inView,entry)} className="cover-elt">
        <h1 className='text-center c-cv  h1-title'><i className="fa-solid fa-grip-lines" />{t('contact')}<i className="fa-solid fa-grip-lines" /></h1>
        <h3>{t('thx')} !</h3>

        <p>... 
        </p>
        <div className="contact-content column j-center align-center margin-1">
            <div className='row align-center contact-elt'>
                <i className="fa-solid fa-user c-cv" />
                <h3>Bah Thierno Mamadou Mouctar</h3>
            </div>

            <div className="row align-center contact-elt">
                <i className="fa-solid fa-location-dot c-cv" />
                <h3>7 rue de lagoé, Siros(64230)</h3>
            </div>

            <div className="row align-center contact-elt">
                <i className="fa-solid fa-phone c-cv" />
                <h3>+33 6 17 99 79 73</h3>
            </div>

            <div className="row align-center contact-elt">
                <i className="fa-solid fa-envelope c-cv" />
                <h3> <a href="mailto:thiernobah1899@gmail.com">thiernobah1899@gmail.com</a></h3>
            </div>
        </div>
        </InView>
    </div>
}

export default Contact;