import React from 'react'
import Contact from './Contact';

const Footer = () => {
    const goUp=()=>{
        window.scrollTo({
        top:0,
        left:0,
        behavior:'smooth'
    });
    }
    return (
        <div>
             <div className="contacts text-light">
                <Contact/>
            </div>
            <div className="float-r c-orange">
            <i className="fa-solid fa-angles-up up" onClick={()=>goUp()}/>
            </div>
            <div className="row j-center align-center text-light">
                Créer par <h3 className='c-orange padding0-1'>Bah Thierno ©</h3> tous droits préservés. 
            </div>
        </div>
    )
}
export default Footer

